import * as React from "react";
import { graphql } from "gatsby";
import {
  FaMobileAlt as FaPhone,
  FaRegEnvelope as FaEnvelope,
  FaInstagram,
} from "react-icons/fa";
import Layout from "../../components/layout/layout.component";
import Seo from "../../components/seo/seo.component";
import Logo from "../../components/logo/logo.component";

const ContactsPage = ({ data }) => {
  const { address, email, instagram, phone } =
    data?.allSanitySettings?.edges?.length > 0
      ? data?.allSanitySettings?.edges[0].node
      : {};
  const name = data?.site?.siteMetadata?.name;

  return (
    <Layout>
      <main className="main container-fluid vertical-align-contact p-0 h-100 d-flex flex-column justify-content-center align-items-center">
        <Logo />
        <address className="text-center">
          {name && (
            <p className="fs-2 name glitch" data-text={name}>
              {name}
            </p>
          )}
          {address && <p className="fs-5 mb-1">{address}</p>}
          {email && (
            <p className="fs-5 mb-1">
              <a href={`mailto:${email}`} className="text-reset">
                <FaEnvelope className="me-1" /> {email}
              </a>
            </p>
          )}
          {instagram && (
            <p className="fs-5 mb-1">
              <a href={`${instagram}`} className="text-reset">
                <FaInstagram className="me-1" /> {instagram}
              </a>
            </p>
          )}
          {phone && (
            <p className="fs-5 mb-1">
              <a href={`tel:${phone}`} className="text-reset">
                <FaPhone className="me-1" /> {phone}
              </a>
            </p>
          )}
        </address>
      </main>
    </Layout>
  );
};

export const query = graphql`
  query {
    site {
      siteMetadata {
        name
      }
    }
    allSanitySettings {
      edges {
        node {
          address
          email
          instagram
          phone
        }
      }
    }
  }
`;

export default ContactsPage;

export const Head = () => {
  return (
    <>
      <Seo pathname="contacts" />
      <body className="contacts p-0" />
    </>
  );
};
